import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			columns: [{label: '',value: ''}],
			clubName: '请选择俱乐部',
			clubId: '',
			money: '',
			userName: '',
			headImg: ''
		}
	},
	created() {
		this.$http.get({
			url: '/v1/rest/club/clubMemberRecharge',
			data: {
				userId: uni.getStorageSync('userInfo').userId
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.headImg = resp.data.headPortrait
				this.userName = resp.data.account
				this.columns = resp.data.labelReturnVoList
			}
		})
	},
	methods: {
		choiceClub() {
			this.$refs.picker1.show()
		},
		onConfirm(v) {
			// console.log(v)
			this.clubId = v.checkArr.value
			this.clubName = v.checkArr.label
		},
		inputMoney(v) {
			this.money = v.detail
		},
		submit() {
			if(this.clubId == '') {
				uni.showToast({
					title: '请选择要充值的俱乐部',
					duration: 2000,
					icon: 'none'
				});
			}else if(Number(this.money) <= 0) {
				uni.showToast({
					title: '请输入您要充值的金额',
					duration: 2000,
					icon: 'none'
				});
			}else{
				const that = this
				this.$http.post({
					url: '/v1/rest/pay/pay',
					data: {
						userId: uni.getStorageSync('userInfo').userId,
						totalPrice: Number(this.money),
						clubOrUserWalletId: this.clubId,
						type: 1  //与后端约定钱包type为0，会费充值type为1
					}
				}).then(resp => {
					console.log(resp)
					if(resp.statusCode == 200) {
						uni.login({
						  provider: 'weixin',
						  success: function (loginRes) {
						    that.$http.get({
						    	url: '/v1/rest/login/updateOpenId',
						    	data:{
						    		code: loginRes.code,
										userId: uni.getStorageSync('userInfo').userId
						    	}
						    }).then(resp => {
									console.log(resp)
									that.submit()
								})
						  }
						});
					}else if(resp.status == 200) {
						this.orderNo = resp.data.orderNo
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: resp.data.timeStamp,
							nonceStr: resp.data.nonceStr,
							package: resp.data.package,
							signType: resp.data.signType,
							paySign: resp.data.paySign,
							success: function (res) {
								console.log(res)
								// 支付成功回调
								that.$http.get({
									url: '/v1/rest/pay/memberWechatPayCallback',
									data: {
										type: 'success',
										orderNo: that.orderNo,
										rechargeType: 1,
										clubId: that.clubId
									}
								}).then(resp => {
									console.log(resp)
									if(resp.status == 200) {
										uni.showToast({
											title: resp.data.message,
											duration: 2000,
											icon: 'none'
										}); 
										const _userId = uni.getStorageSync('userInfo').userId
										uni.redirectTo({
											url: '/pages/groupOwnerManage/membershipDetails/membershipDetails?clubId=' + that.clubId + '&userId=' + _userId
										})
									}
								})
							},
							fail: function (err) {
								console.log(err)
								// 支付取消回调
								that.$http.get({
									url: '/v1/rest/pay/memberWechatPayCallback',
									data: {
										type: 'fail',
										orderNo: that.orderNo,
										rechargeType: 1,
										clubId: that.clubId
									}
								}).then(resp => {
									console.log(resp)
									if(resp.status == 200) {
										uni.showToast({
											title: resp.data.message,
											duration: 2000,
											icon: 'none'
										});
									}
								})
							}
						});
					}
				})
			}
		}
	}
}
